@import url("https://fonts.googleapis.com/css2?family=Rubik+Marker+Hatch&display=swap");
.landingPage {
  width: 100%;
  height: 100vh;
  background-image: url(../images/80-805573_darksiders-2-protagonist.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  opacity: 90%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Logo */
.landing__logo {
  padding: 20px;
}

.landing__logo img {
  width: 350px;
}

/* Parrafo */
.landing__parragraf {
  padding-top: 10px;
  color: #ffefd5;
  width: 0;
  overflow: hidden;
  white-space: nowrap;
  font-size: 3rem;
  margin: 0 auto;
  font-family: "Rubik Marker Hatch", cursive;
  font-weight: 400;
  border-right: 0.15em solid transparent;
  animation: typing 4s steps(38) 1s 1 normal both, blink 1s steps(1) infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}

/*  Button Init */
.landing__init {
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.landing__init_btn {
  min-width: 300px;
  min-height: 60px;
  font-family: "Nunito", sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  font-weight: 700;
  color: #313133;
  background: #4fd1c5;
  background: linear-gradient(
    90deg,
    rgba(129, 230, 217, 1) 0%,
    rgba(79, 209, 197, 1) 100%
  );
  border: none;
  border-radius: 1000px;
  box-shadow: 12px 12px 24px rgba(79, 209, 197, 0.64);
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  position: relative;
  padding: 10px;
}

.landing__init_btn::before {
  content: "";
  border-radius: 1000px;
  min-width: calc(300px + 12px);
  min-height: calc(60px + 12px);
  border: 6px solid #00ffcb;
  box-shadow: 0 0 60px rgba(0, 255, 203, 0.64);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 0.3s ease-in-out 0s;
}

.landing__init_btn:hover,
.landing__init_btn:focus {
  color: #313133;
  transform: translateY(-6px);
}

button:hover::before,
button:focus::before {
  opacity: 1;
}

button::after,
button::after {
  content: "";
  width: 30px;
  height: 30px;
  border-radius: 100%;
  border: 6px solid #00ffcb;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: ring 1.5s infinite;
}

button:hover::after,
button:focus::after {
  animation: none;
  display: none;
}

@keyframes ring {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }
  100% {
    width: 300px;
    height: 300px;
    opacity: 0;
  }
}
